html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  color: #333;
  background-color: #293749;
  font-family: Helvetica, 'Noto Sans', 'Segoe UI', 'Noto Sans CJK TC', 'sourcesanspro', '微軟正黑體', Arial, sans-serif;
  font-weight: 300;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  margin-block-start: 0.5rem;
  margin-block-end: 0.5rem;
}

#particles-js {
  position: absolute;
  width: 99%;
  height: 99%;
}

.container {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 100%;
  margin: 20px;
  max-width: 720px;
  background: #eee;
  border-radius: 1rem;
  z-index: 10;
  padding: 1rem;
}

.hide{
  opacity: 0;
  transition: all 1s ease-out;
}

.cardTop {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.avatar {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-image: url('assets/avatar.png');
  background-size: cover;
}

.basicInfo {
  text-align: left;
  margin: 0 2rem;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skill {
  flex: 1;
  margin: 0.5rem;
  background-color: #f7f7f7;
  border-radius: 8px;
  padding: 1rem;
}

.skill ul {
  padding-inline-start: 1rem;
}

.skill-title {
  font-weight: 700;
  color: #273b55;
}

.skill li {
  list-style: circle;
  line-height: 2rem;
  margin: 0 0.5rem;
}

.cardButtom {
  margin: 30px 40px;
}

.buttons {
  display: flex;
  justify-content: center;
  list-style: none;
  padding-left: 0;
}

.buttons > .btn {
  text-decoration: none;
  background-color: #f7f7f7;
  font-size: 1.2rem;
  font-weight: 400;
  color: #2CD5C4;
  border: 2px solid #eee;
  border-radius: 6px;
  margin: 0 1rem ;
  padding: 10px 24px;
  transition: all 0.3s;
}

.buttons > .btn:hover {
  background-color: #fff;
  box-shadow: 2px 2px 4px rgba(0,0,0,0.03);
}